<template>
  <v-card
    flat
    style="width: 100px;"
    class="pointer is-image-box ma-1 py-8 px-2 d-flex align-center"
  >
    <v-img v-if="image" width="100px" :src="image">
      <v-btn
        icon
        @click="deleteImage"
      >
        <v-icon class="white--text">mdi-delete</v-icon>
      </v-btn>
    </v-img>

    <div
      v-else
      class="primary--text text-center centering"
      @click="chooseImage"
    >
      <span
        class="text-center"
      >
        <v-icon class="" color="primary">{{ icon }}</v-icon><br />
        <span v-if="icon == 'mdi-image'" class="text-caption">افزودن عکس</span>
      </span>
    </div>

    <input
      hidden
      ref="fileInput"
      accept="image/*"
      type="file"
      @input="onSelectImage"
    />
  </v-card>
</template>

<script>
import authHeader from "@/middleware/authHeader.js";

export default {
  name: "ImageInputComponent",

  props: {
    icon: {
      type: String,
      default: 'mdi-image',
    }
  },
  components: {},
  data: () => ({
    image: null,
    filenewname: null,
  }),
  methods: {
    chooseImage() {
      this.$refs.fileInput.click();
    },
    async onSelectImage() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0]) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.image = e.target.result;
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
        const fd = new FormData();
        fd.append("image", files[0]);
        const result = await this.$axios.post("/main/upload", fd, authHeader());
        if (result.data.success) {
          this.filenewname = result.data.files[0]
          this.$emit("image-inputs", this.filenewname);
        }
      }
    },
    async deleteImage() {
      this.$emit("remove-image-input", this.filenewname);
      await this.$axios.post("/main/upload/delete-file",{ filename: this.filenewname }, authHeader());
      this.filenewname = null;
      this.image = null;
      this.$refs.fileInput.value = null;
    },
  },
};
</script>

<style scoped>
/* .is-image-box {
  border-width: 2px;
  border-style: dashed;
  border-color: #fb9517;
} */
.is-image-box {
  border:dashed 3px hsla(0, 95%, 35%, 1);
}
.centering {
  margin-left: auto;
  margin-right: auto;
}
</style>
